/* eslint-disable react/jsx-filename-extension */
/* eslint-disable import/extensions */
/* eslint-disable max-len */
import React, { useState } from 'react';
import Button from 'elements/Button';
import { Fade } from 'react-awesome-reveal';

export default function Contact() {
  const [form, setForm] = useState({
    name: '',
    email: '',
    phone: '',
    message: '',
  });

  const [errors, setErrors] = useState({});
  const [sent, setSent] = useState(false);

  const handleChange = (e) => setForm({ 
    ...form,
    [e.target.name]: e.target.value,
  });

  const validate = () => {
    const e = {};

    if (!form.name.trim()) e.name = 'Name is required.';
    if (!form.email.trim()) e.email = 'Email is required.';
    else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.email.trim())) {
      e.email = 'Enter a valid email.';
    }
    if (!form.message.trim()) e.message = 'Message is required.';

    setErrors(e);
    return Object.keys(e).length === 0;
  };

  const handleSubmit = (evt) => {
    evt.preventDefault();

    if (!validate()) return;

    // Simulate sending
    setTimeout(() => {
      setSent(true);
      setForm({
        name: '',
        email: '',
        phone: '',
        message: '',
      });
      setErrors({});
    }, 500);
  };
  return (
    <>
      {/* HERO */}
      <section className="py-20 bg-gradient-to-b from-white to-gray-50">
        <div className="container mx-auto px-6 text-center">
          <Fade triggerOnce direction="up">
            <h1 className="text-5xl font-bold text-theme-blue mb-4">
              Contact Us
            </h1>
            <p className="text-lg text-gray-600 max-w-2xl mx-auto">
              We&apos;re here to help  support, questions, partnerships, and more.
            </p>
          </Fade>
        </div>
      </section>

      {/* CONTENT */}
      <section className="py-16">
        <div className="container mx-auto px-6 grid grid-cols-1 lg:grid-cols-2 gap-12">

          {/* LEFT SIDE */}
          <Fade triggerOnce direction="left">
            <div className="space-y-8">
              <h2 className="text-3xl text-theme-blue font-bold">Get in Touch</h2>
              <p className="text-gray-600 text-lg">
                Reach out anytime  we respond quickly.
              </p>

              <div className="space-y-3">
                <p className="text-gray-700 text-lg">
                   <b>Email:</b> info@fajtech.ca
                </p>
                <p className="text-gray-700 text-lg">
                   <b>Phone:</b> +20 120 601 0086
                </p>
                <p className="text-gray-700 text-lg">
                   <b>HQ:</b> Cairo, Egypt
                </p>
              </div>

              <h3 className="text-2xl font-semibold text-theme-blue mt-10">
                Working Hours
              </h3>
              <p className="text-gray-600">Sunday  Thursday: 9am  6pm</p>
              <p className="text-gray-600">Friday  Saturday: Closed</p>
            </div>
          </Fade>

          {/* RIGHT SIDE  FORM */}
          <Fade triggerOnce direction="right">
            <div className="bg-white shadow-xl rounded-2xl p-8 border">
              <h3 className="text-2xl font-bold mb-6 text-theme-blue">
                Send Us a Message
              </h3>

              {sent && (
                <div className="mb-6 p-4 bg-green-100 text-green-700 rounded-lg">
                  Your message has been sent! We will reply shortly.
                </div>
              )}

              <form onSubmit={handleSubmit} className="space-y-6" noValidate>

                {/* Full Name */}
                <div>
                  <label htmlFor="block text-sm font-medium text-gray-700">
                    Full Name
                    <input
                      name="name"
                      value={form.name}
                      onChange={handleChange}
                      className={`mt-2 block w-full border rounded-md p-3 ${
                        errors.name ? 'border-red-500' : 'border-gray-300'
                      }`}
                      placeholder="John Doe"
                      autoComplete="name"
                      required
                    />
                  </label>
                  {errors.name && (
                    <p className="text-sm text-red-600 mt-1">{errors.name}</p>
                  )}
                </div>

                {/* Email */}
                <div>
                  <label htmlFor="block text-sm font-medium text-gray-700">
                    Email Address
                    <input
                      name="email"
                      type="email"
                      value={form.email}
                      onChange={handleChange}
                      className={`mt-2 block w-full border rounded-md p-3 ${
                        errors.email ? 'border-red-500' : 'border-gray-300'
                      }`}
                      placeholder="you@example.com"
                      autoComplete="email"
                      required
                    />
                  </label>
                  {errors.email && (
                    <p className="text-sm text-red-600 mt-1">{errors.email}</p>
                  )}
                </div>

                {/* Phone */}
                <div>
                  <label htmlFor="block text-sm font-medium text-gray-700">
                    Phone (optional)
                    <input
                      name="phone"
                      value={form.phone}
                      onChange={handleChange}
                      className="mt-2 block w-full border rounded-md p-3 border-gray-300"
                      placeholder="+20 123 456 7890"
                      autoComplete="tel"
                    />
                  </label>
                </div>

                {/* Message */}
                <div>
                  <label htmlFor="block text-sm font-medium text-gray-700">
                    Message
                    <textarea
                      name="message"
                      rows="5"
                      value={form.message}
                      onChange={handleChange}
                      className={`mt-2 block w-full border rounded-md p-3 ${
                        errors.message ? 'border-red-500' : 'border-gray-300'
                      }`}
                      placeholder="How can we help you?"
                      required
                    />
                  </label>
                  {errors.message && (
                    <p className="text-sm text-red-600 mt-1">{errors.message}</p>
                  )}
                </div>

                <Button
                  type="submit"
                  className="px-6 py-3 bg-theme-purple text-white rounded-full hover:bg-dark-theme-purple transition w-full"
                >
                  Send Message
                </Button>
              </form>
            </div>
          </Fade>
        </div>
      </section>

    </>
  );
}
