import React, { useState, useEffect } from "react";

const words = ["deep", "reliable", "suitable"];

export default function TypingWords() {
  const [index, setIndex] = useState(0);       // which word
  const [subIndex, setSubIndex] = useState(0); // typing progress
  const [deleting, setDeleting] = useState(false);
  const [blink, setBlink] = useState(true);

  useEffect(() => {
    // blinking cursor
    const timeout2 = setInterval(() => {
      setBlink((v) => !v);
    }, 500);

    return () => clearInterval(timeout2);
  }, []);

  useEffect(() => {
    if (!deleting && subIndex === words[index].length) {
      setTimeout(() => setDeleting(true), 1000);
      return undefined;  // <-- FIX
    }

    if (deleting && subIndex === 0) {
      setDeleting(false);
      setIndex((prev) => (prev + 1) % words.length);
      return undefined;  // <-- FIX
    }

    const timeout = setTimeout(() => {
      setSubIndex((prev) => prev + (deleting ? -1 : 1));
    }, deleting ? 60 : 120);

    return () => clearTimeout(timeout); // valid cleanup
  }, [subIndex, deleting, index]);

  return (
    <span className="text-theme-purple">
      {words[index].substring(0, subIndex)}
      <span className="inline-block w-[3px]">
        {blink ? "|" : " "}
      </span>
    </span>
  );
}
