/* eslint-disable react/jsx-filename-extension */
import React from "react";
import PropTypes from "prop-types";
import { Fade } from "react-awesome-reveal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleCheck } from "@fortawesome/free-solid-svg-icons";

export default function BenefitsSection({
  title,
  subtitle,
  benefits,
  gainsTitle,
  gains,
}) {
  return (
    <section className="py-16">
      <div className="container mx-auto px-6 max-w-4xl">

        {/* WHY CHOOSE */}
        <Fade direction="up" triggerOnce>
          <h2 className="text-3xl lg:text-4xl text-theme-blue font-extrabold mb-4 text-center">
            {title}
          </h2>

          {subtitle && (
            <p className="text-gray-600 mb-10 text-center">{subtitle}</p>
          )}
        </Fade>

        <ul className="space-y-6 text-gray-600 mb-16">
          {benefits.map((item) => (
            <Fade key={item.id} direction="up" delay={item.delay} triggerOnce>
              <li className="flex items-start gap-4">
                <span className="text-2xl leading-none mt-1">
                  <FontAwesomeIcon
                    icon={faCircleCheck}
                    style={{ color: "#47d14a" }}
                  />
                </span>
                <div>
                  <p className="font-semibold">{item.title}</p>
                  <p className="text-sm">{item.text}</p>
                </div>
              </li>
            </Fade>
          ))}
        </ul>

        {/* WHAT YOULL GAIN (BELOW) */}
        <Fade direction="up" triggerOnce>
          <div className="bg-white rounded-2xl shadow-md p-8">
            <h3 className="text-2xl font-semibold text-theme-blue mb-6 text-center">
              {gainsTitle}
            </h3>

            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 text-gray-600">
              {gains.map((gain) => (
                <p key={gain.id} className="flex items-center gap-3">
                  <FontAwesomeIcon
                    icon={faCircleCheck}
                    style={{ color: "#47d14a" }}
                  />
                  <strong>{gain.text}</strong>
                </p>
              ))}
            </div>
          </div>
        </Fade>

      </div>
    </section>
  );
}

BenefitsSection.propTypes = {
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string,
  gainsTitle: PropTypes.string.isRequired,

  benefits: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      text: PropTypes.string.isRequired,
      delay: PropTypes.number,
    }),
  ).isRequired,

  gains: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      text: PropTypes.string.isRequired,
    }),
  ).isRequired,
};

BenefitsSection.defaultProps = {
  subtitle: "",
};
