import React from "react";
import { Route, Routes, Outlet } from "react-router-dom";
import ScrollToTop from "components/ScrollToTop";
import Header from "parts/Header";
import Footer from "parts/Footer";
import UniversalHero from "parts/UniversalHero";

import LandingPage from "pages/LandingPage";
import NotFoundPage from "pages/NotFoundPage";
import TeamPage from "pages/TeamPage";
// contact
import Contact from 'pages/contact';

// services_pages
import ServicesPage from "pages/services/ServicesPage";
import CloudSolutions from "pages/services/Cloud_Solutions";
import ManagedITServices from "pages/services/Managed_IT_Services";
import CybersecurityServices from "pages/services/Cybersecurity_Services";
import HardwareAsAService from "pages/services/Hardware-as-a-Service";
import DisasterRecovery from "pages/services/Disaster_recovery";
import ITConsulting from "pages/services/IT_Consulting";
import VCioServices from "pages/services/vCIO_Services";
import NetworkSecurity from "pages/services/Network_Security";
import HostedPrivateCloud from "pages/services/Hosted_Private_Cloud";
import ManagedPrintServices from "pages/services/Managed_Print_Services";
import RoboticProcessAutomation from "pages/services/Robotic_Process_Automation";
import ServiceDetailPage from "pages/services/ServicesDetailPage";

// Industries_pages
import IndustriesPage from "pages/Industries/Industries";
import IndustryDetailPage from "pages/Industries/IndustryDetailPage";
import Audiology from "pages/Industries/Audiology";
import Automotive from "pages/Industries/Automotive";
import Aviation from "pages/Industries/Aviation";
import EducationTraining from "pages/Industries/education-and-training-institutions";
import FinancialServices from "pages/Industries/Financial_Services_and_Accounting_Firms";
import HealthcareMedical from "pages/Industries/Healthcare_and_Medical_Practices";
import HospitalityTourism from "pages/Industries/Hospitality_and_Tourism";
import LegalFirms from "pages/Industries/legal_Firms";
import LocalGovMunicipal from "pages/Industries/Local_Governments_and_Municipalities";
import ManufacturingLogistics from "pages/Industries/Manufacturing_and_Logistics";
import NonprofitOrganizations from "pages/Industries/Nonprofit_Organizations";
import ProfessionalServices from "pages/Industries/professional-services-consulting-marketing-hr";
import RealEstateProperty from "pages/Industries/Real_Estate_and_Property_Management";
import RetailEcommerce from "pages/Industries/Retail_and_Ecommerce";

// about_pages
import About from 'pages/about/about';
import OurMissionAndValues from "pages/about/Our_Mission_And_Values";
import AwardsAndRecognition from "pages/about/Awards_And_Recognition";
import EWasteRecycling from "pages/about/E_Waste_Recycling";
import Careers from "pages/about/Careers";
import EventsHub from "pages/about/Events_Hub";
import FAQs from "pages/about/FAQs";

/* ---------- Booking pages ---------- */
import BookConsultation from "pages/Book_Consultation";
import BookConsultationThanks from "pages/book-consultation/Thanks";

import "./assets/css/styles.css";

/* Layout that ensures Header -> UniversalHero -> Outlet -> Footer */
function Layout() {
  return (
    <>
      <Header />
      <UniversalHero />
      <main className="relative z-0">
        <Outlet />
      </main>
      <Footer />

    </>
  );
}

function App() {
  return (
    <>
      {/* Keep ScrollToTop outside Routes */}
      <ScrollToTop />

      <Routes>
        {/* Wrap everything with the Layout so hero appears under header */}
        <Route path="/" element={<Layout />}>
          {/* Landing (home) */}
          <Route index element={<LandingPage />} />
          {/* Contact */}
          <Route exact path="/contact" element={<Contact />} />

          {/* Services */}
          <Route path="services" element={<ServicesPage />} />
          <Route path="services/:slug" element={<ServiceDetailPage />} />
          <Route path="services/cloud-solutions" element={<CloudSolutions />} />
          <Route path="services/managed-it-services" element={<ManagedITServices />} />
          <Route path="services/cybersecurity-services" element={<CybersecurityServices />} />
          <Route path="services/hardware-as-a-service" element={<HardwareAsAService />} />
          <Route path="services/disaster-recovery" element={<DisasterRecovery />} />
          <Route path="services/it-consulting" element={<ITConsulting />} />
          <Route path="services/vcio-services" element={<VCioServices />} />
          <Route path="services/network-security" element={<NetworkSecurity />} />
          <Route path="services/hosted-private-cloud" element={<HostedPrivateCloud />} />
          <Route path="services/managed-print-services" element={<ManagedPrintServices />} />
          <Route path="services/robotic-process-automation" element={<RoboticProcessAutomation />} />

          {/* Industries */}
          <Route path="industries" element={<IndustriesPage />} />
          <Route path="industries/:slug" element={<IndustryDetailPage />} />
          <Route path="industries/audiology" element={<Audiology />} />
          <Route path="industries/automotive" element={<Automotive />} />
          <Route path="industries/aviation" element={<Aviation />} />
          <Route path="industries/education-and-training-institutions" element={<EducationTraining />} />
          <Route path="industries/financial-services-and-accounting-firms" element={<FinancialServices />} />
          <Route path="industries/healthcare-and-medical-practices" element={<HealthcareMedical />} />
          <Route path="industries/hospitality-and-tourism" element={<HospitalityTourism />} />
          <Route path="industries/legal-firms" element={<LegalFirms />} />
          <Route path="industries/local-governments-municipalities" element={<LocalGovMunicipal />} />
          <Route path="industries/manufacturing-and-logistics" element={<ManufacturingLogistics />} />
          <Route path="industries/nonprofit-organizations" element={<NonprofitOrganizations />} />
          <Route path="industries/professional-services-consulting-marketing-hr" element={<ProfessionalServices />} />
          <Route path="industries/real-estate-and-property-management" element={<RealEstateProperty />} />
          <Route path="industries/retail-and-ecommerce" element={<RetailEcommerce />} />

          {/* about */}
          <Route exact path="/about" element={<About />} />
          <Route path="about/our-mission-values" element={<OurMissionAndValues />} />
          <Route path="about/awards-recognition" element={<AwardsAndRecognition />} />
          <Route path="about/e-waste-recycling" element={<EWasteRecycling />} />
          <Route path="about/careers" element={<Careers />} />
          <Route path="about/events-hub" element={<EventsHub />} />
          <Route path="about/faqs" element={<FAQs />} />

          {/* Booking */}
          <Route path="book-consultation" element={<BookConsultation />} />
          <Route path="book-consultation/thanks" element={<BookConsultationThanks />} />

          {/* Team */}
          <Route path="team" element={<TeamPage />} />

          {/* Catch-all */}
          <Route path="*" element={<NotFoundPage />} />
        </Route>
      </Routes>
    </>
  );
}

export default App;
