import React from 'react';
import { useParams, Link } from 'react-router-dom';
import Services from '../../data/services';

import slugify from '../../utils/slugify';

export default function ServiceDetailPage() {
  const { slug } = useParams();
  const title = Services.find((s) => slugify(s) === slug);

  return (
      <section className="container mx-auto px-4 py-12">
        {!title ? (
          <div>
            <h1 className="text-3xl font-bold">Service not found</h1>
            <p className="mt-4">couldn&apos;t find that service.</p>
            <Link to="/services" className="text-theme-purple underline mt-4 inline-block">Back to services</Link>
          </div>
        ) : (
          <div>
            <h1 className="text-4xl font-bold text-theme-blue">{title}</h1>
            <p className="mt-4 text-gray-600">
              {/* Placeholder content  replace with real copy per-service */}
              This page is a placeholder for <strong>{title}</strong>. Add detailed content, images or links here.
            </p>
            <div className="mt-8">
              <Link to="/services" className="text-theme-purple underline">Back to services</Link>
            </div>
          </div>
        )}
      </section>
  );
}
