/* eslint-disable react/jsx-filename-extension */
/* eslint-disable max-len */

import React from 'react';
import { useParams, Link } from 'react-router-dom';

import Industries from 'data/industries';
import { Fade } from 'react-awesome-reveal';
import slugify from '../../utils/slugify';

/**
 * IndustryDetailPage
 * /industries/:slug
 * Matches layout of your service detail pages
 */
export default function IndustryDetailPage() {
  const { slug } = useParams();
  const index = Industries.findIndex((name) => slugify(name) === slug);
  const name = index !== -1 ? Industries[index] : null;

  // LOAD IMAGE IF EXISTS
  let imgSrc = null;
  if (name) {
    try {
      // eslint-disable-next-line global-require, import/no-dynamic-require
      imgSrc = require(`assets/images/Industries/${slug}.png`);
    } catch (e) {
      imgSrc = null;
    }
  }

  if (!name) {
    return (
          <section className="container mx-auto px-4 py-20 text-center">
          <h1 className="text-4xl font-bold text-theme-blue mb-4">Industry Not Found</h1>
          <p className="text-gray-500 mb-6">The industry you were looking for does not exist.</p>
          <Link to="/industries" className="text-theme-purple hover:underline">Back to Industries</Link>
        </section>
    );
  }

  const description = `${name} organizations benefit heavily from tailored IT solutions  from cybersecurity and cloud infrastructure to compliance and business continuity. Explore how our services support the ${name.toLowerCase()} sector.`;

  return (
    <>

      {/* HERO */}
      <Fade direction="up" triggerOnce>
        <section className="hero py-16">
          <div className="container mx-auto px-6 lg:flex lg:items-center">

            <div className="lg:w-1/2 mb-6 lg:mb-0">
              <h1 className="text-4xl sm:text-5xl font-bold text-theme-blue mb-6">{name}</h1>
              <p className="text-gray-600 mb-6">{description}</p>

              <div className="flex gap-4">
                <Link to="/services" className="px-6 py-3 bg-theme-purple text-white rounded-full">View Services</Link>
                <Link to="/contact" className="px-6 py-3 border border-gray-200 rounded-full text-theme-blue">Contact Us</Link>
              </div>
            </div>

            <div className="lg:w-1/2 flex items-center justify-center">
              {imgSrc ? (
                <img src={imgSrc} alt={name} className="max-w-full h-64 object-contain rounded-xl shadow-xl" />
              ) : (
                <div className="w-full h-64 bg-gray-50 rounded-xl flex items-center justify-center">
                  <svg className="w-24 h-24 text-gray-300" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 7v10a2 2 0 002 2h14a2 2 0 002-2V7" />
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7V4h8v3" />
                  </svg>
                </div>
              )}
            </div>

          </div>
        </section>
      </Fade>

      {/* MAIN CONTENT */}
      <section className="container mx-auto px-4 py-16">

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">

          {/* LEFT COLUMN */}
          <div className="lg:col-span-2">
            <h2 className="text-2xl font-bold text-theme-blue mb-4">
              How We Support the {name} Industry
            </h2>

            <p className="text-gray-600 mb-6 leading-relaxed">
              We design IT strategies that support the mission and operations of {name.toLowerCase()} organizations
              helping them scale, secure their data, improve uptime, and achieve compliance requirements.
            </p>

            <h3 className="text-xl font-semibold text-theme-blue mb-2">
              What Youll Gain
            </h3>

            <ul className="list-disc ml-5 text-gray-600 leading-loose">
              <li>Improved uptime, reliability, and performance</li>
              <li>Stronger cybersecurity posture</li>
              <li>Optimized cloud and infrastructure costs</li>
              <li>Better business continuity and disaster readiness</li>
            </ul>
          </div>

          {/* RIGHT COLUMN */}
          <aside className="bg-white rounded-2xl shadow-lg p-6">
            <h4 className="text-lg font-semibold text-theme-blue mb-3">Related Services</h4>

            <ul className="space-y-3 text-gray-600">
              <li><Link to="/services/managed-it-services" className="text-theme-purple hover:underline">Managed IT Services</Link></li>
              <li><Link to="/services/network-security" className="text-theme-purple hover:underline">Network Security</Link></li>
              <li><Link to="/services/cloud-solutions" className="text-theme-purple hover:underline">Cloud Solutions</Link></li>
            </ul>
          </aside>

        </div>

      </section>

    </>
  );
}
