/* eslint-disable react/jsx-filename-extension */
/* eslint-disable import/extensions */

import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import Button from '../elements/Button';

export default function CTAFloatCard({
  title,
  description,
  buttonText,
  buttonLink,
}) {
  const ref = useRef(null);
  const [visible, setVisible] = useState(false);

  // Show on scroll (once)
  useEffect(() => {
    const el = ref.current;
    if (!el) return undefined;

    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setVisible(true);
          observer.disconnect();
        }
      },
      { threshold: 0.25 },
    );

    observer.observe(el);

    return () => {
      observer.disconnect();
    };
  }, []);

  return (
    <section
      ref={ref}
      className={`
        relative z-20 -mt-52 px-2 pb-2
        transition-all duration-900 ease-out
        ${visible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-12'}
      `}
    >
      <div className="max-w-4xl mx-auto bg-white rounded-2xl shadow-2xl px-8 py-12 text-center">

        <h2 className="text-3xl sm:text-4xl font-extrabold text-gray-900 mb-4">
          {title}
        </h2>

        <p className="text-gray-600 text-base sm:text-lg max-w-2xl mx-auto mb-8">
          {description}
        </p>

        <Button
          href={buttonLink}
          type="link"
          className="inline-flex items-center justify-center
            bg-green-600 hover:bg-green-700
            text-white font-semibold
            px-8 py-4
            rounded-lg
            shadow-lg
            transition"
        >
          {buttonText}
        </Button>

      </div>
    </section>
  );
}

CTAFloatCard.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  buttonText: PropTypes.string,
  buttonLink: PropTypes.string,
};

CTAFloatCard.defaultProps = {
  buttonText: 'BOOK A CONSULTATION',
  buttonLink: '/book-consultation',
};
