/* small slugify util used in Header/App */
function slugify(text) {
  return text
    .toString()
    .toLowerCase()
    .trim()
    .replace(/[\s_]+/g, '-')        // spaces/underscores -> dash
    .replace(/[^\w-]+/g, '')        // remove non-word chars except dash
    .replace(/--+/g, '-');          // collapse multiple dashes
}

